body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  padding: 20px;
  background-color:black;
  color:white;
  display:flex;
  justify-content: space-between;

}
.cart-icon .counter{
  height:18px;
  width:18px;
  background-color:blue;
  border-radius:50px;
  position:relative;
  display:flex;
  align-items:center;
  justify-content:center;
  right:7px;
  top:-6px;

}
img{
  height:200px;
}
.cart-icon{
  display: flex;
}
.icon{
  
  font-size:22px;
}
.cart-wrapper{
  display: inline-flex;
  
  width:320px;
  box-shadow:0px 0px 7px black;
  border-radius: 5px;
  
  
}
.image-wrapper{
  width:180px;
  display:flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right:1px solid rgb(91, 89, 89);
}
.content{
  flex-grow: 1;
  display:flex;
  flex-direction: column;
  gap:10px;
  padding:10px;

}
.price{
  margin-bottom:auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius:3px;
}
.quantity{
  display:flex;
  justify-content: space-between;
  align-self:flex-end;
  border-radius: 3px;
  border:1px solid grey;
}
.quantity *{
  height:22px;
  width:22px;
  display:flex;
  align-items:center;
  justify-content: center;
}
.quantity > div:first-child,
.quantity > button
{
  background-color:darkblue;
  color:white;
  border-radius:none!;

}
.quantity>button:disabled
{
  opacity: 0.7;
}
main{
  padding:20px;
  display:flex;
  flex-wrap:wrap;
  min-height:80vh;
  justify-content: center;
  align-items: center;
  align-content:center;
  gap:50px
}
.total{
  box-sizing:border-box;
  padding:17px 30px;
  background-color:green;
  position:fixed;
  bottom:0;
  width:100%;
  text-align:right;
}
